import React, { useState , useEffect  } from "react";
import { useLanguage } from '../components/LanguageContext';
import Papa from 'papaparse';

import { faTruck,faStar,faCircleChevronRight,faXmark } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import MoneyIcon from '../assets/MoneyIcon.png';
import CommunityIcon from '../assets/communityIcon.png';
import EcoIcon from '../assets/EcoIcon.png';

import defaultProfile from '../assets/default-profile.png'


import starReviews3 from '../assets/star3.png';
import starReviews5 from '../assets/star5.png';
import starReviews4 from '../assets/star4.png'; 
import circleRight from '../assets/circleRight.png';
import qrCode from '../assets/qrCode.png';;


const ProductPage = () => {
  const { language } = useLanguage();
  const [products, setProducts] = useState([]);
  const [selectedProduct, setSelectedProduct] = useState(null);
  const [currentImage, setCurrentImage] = useState(null);
  const [activeSmells, setActiveSmells] = useState([]);
  const [selectedTier, setSelectedTier] = useState(null);
  const [averageStars, setAverageStars] = useState(0);
  const [showModal, setShowModal] = useState(false);
  const [isMobile, setIsMobile] = useState(false);
  
  useEffect(() => {
    window.addEventListener('resize', handleResize);
    handleResize(); // Initial check
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const handleResize = () => {
    setIsMobile(window.innerWidth <= 1024);
  };


  useEffect(() => {
    // คำนวณค่าเฉลี่ยดาวเมื่อเปลี่ยนสินค้า
    if (selectedProduct?.textWithTier) {
      setAverageStars(calculateAverageStars(selectedProduct.textWithTier));
    }
  }, [selectedProduct]);

  
  const countReviewsByTier = (tier) => {
  return selectedProduct?.textWithTier.filter(review => review.tier === tier).length || 0;
};
  const handleTierSelect = (tier) => {
    // ฟังก์ชันในการตั้งค่า tier ที่เลือก
    setSelectedTier(tier);
  };

  const handleProductClick = (productId) => {
    const selected = products.find(product => product.id === productId);
    setSelectedProduct(selected);
    
    // ตรวจสอบว่า selectedProduct และ images มีค่าและเป็น array ก่อนเข้าถึง
    if (selected && Array.isArray(selected.images) && selected.images.length > 0) {
      setCurrentImage(selected.images[0]);
    } else {
      // ถ้าไม่มี images หรือ images เป็น array ว่าง
      setCurrentImage(null);
    }
  };

  const calculateAverageStars = (reviews) => {
    const totalStars = reviews.reduce((acc, review) => acc + review.tier, 0);
    const average = reviews.length > 0 ? (totalStars / reviews.length).toFixed(1) : 0;
  
    // ตัด .0 ออกถ้ามี
    return parseFloat(average) % 1 === 0 ? parseInt(average) : average;
  };

  const handleClick = () => {
    setShowModal(true);
  };

  
    
 const [isLoading, setIsLoading] = useState(false);

 const fetchData = async () => { 
  const sheetUrl = 'https://docs.google.com/spreadsheets/d/e/2PACX-1vTInD2_oJWI2ZY5k_Be_85w15vQXiScK-qYoMQlaRb6zAfJHVlkVTklL0yLAzm5iWjs_U42ydTQdCcX/pub?output=csv';

  setIsLoading(true); // เริ่มโหลด
  try {
    const response = await fetch(sheetUrl);
    const data = await response.text();

    Papa.parse(data, {
      header: false,
      dynamicTyping: true,
      skipEmptyLines: true,
      worker: true,
      complete: (result) => {
        const productData = result.data.map(row => {
          const images = row[7] ? row[7].split(',').map(image => image.trim()) : [];
          const descriptionEN = row[5] || ''; 
          const descriptionTH = row[6] || ''; 
          
          // ตรวจสอบและแยกค่า tier
          const tierString = row[8] || ''; 
          const tier = tierString
            .split(',') 
            .map(t => {
              const trimmed = t.trim(); 
              return isNaN(trimmed) ? null : Number(trimmed); 
            })
            .filter(t => t !== null); 

          const text = row[9] || ''; 
          const textArray = text.split(','); 

          const textWithTier = tier.map((tierValue, index) => {
            const reviewText = textArray[index] || ''; 
            return { tier: tierValue, review: reviewText };
          });

          const averageTier = tier.length 
                              ? tier.reduce((sum, current) => sum + current, 0) / tier.length 
                              : 0;

          const smellTH = row[10] ? row[10].split(',').map(smell => smell.trim()) : []; 
          const smellEN = row[11] ? row[11].split(',').map(smell => smell.trim()) : []; 

          return {
            id: row[0] || '',
            name: row[1] || '',
            nameEN: row[2] || '',
            price: row[3] || '',
            caption: row[4] || '',
            descriptionEN: descriptionEN,
            descriptionTH: descriptionTH,
            images: images,
            tier: tier,
            textWithTier: textWithTier,
            averageTier: averageTier,
            smellTH: smellTH, 
            smellEN: smellEN, 
          };
        });

        // กรองข้อมูลโดยใช้ id ที่ไม่เป็น NaN
        const filteredProductData = productData.filter(product => product.id && !isNaN(product.id));
        console.log(filteredProductData);

        const sortedProductData = filteredProductData.sort((a, b) => a.id - b.id);

        setProducts(sortedProductData); // อัปเดต state
        setIsLoading(false); // เสร็จสิ้นการโหลด

        // Select the first product as the default selection
        if (sortedProductData.length > 0) {
          handleProductClick(sortedProductData[0].id); // Set the first product as selected
        }
      },
      error: (error) => {
        console.error('Error parsing CSV:', error);
        setIsLoading(false);
      }
    });
  } catch (error) {
    console.error('Error fetching data:', error);
    setIsLoading(false);
  }
};


  const renderStars = (tier) => {
    const totalStars = 5; // จำนวนดาวทั้งหมด
    let stars = [];
  
    for (let i = 0; i < totalStars; i++) {
      if (i < tier) {
        // ดาวเต็ม
        stars.push(<FontAwesomeIcon key={`filled-${i}`} icon={faStar} style={{ color: '#FFB53E' }} />);
      } else {
        // ดาวว่าง
        stars.push(<FontAwesomeIcon key={`empty-${i}`} icon={faStar} style={{ color: '#FFFFFF' }} />);
      }
    }
  
    return stars;
  };
  useEffect(() => {
    fetchData();
  }, []); // เรียก fetchData ครั้งเดียวตอน component mount
  
  useEffect(() => {
    if (products.length > 0 && !selectedProduct) {
      // เลือกสินค้าแรกเมื่อข้อมูลสินค้าถูกโหลดเสร็จ และยังไม่มีสินค้าถูกเลือก
      handleProductClick(products[0].id);
    }
  }, [products, selectedProduct]); // ตรวจสอบเมื่อ products หรือ selectedProduct เปลี่ยน
  

  const btnSmell = {
    background: 'white',
    border: '1px solid #949494',
    padding: '0 10px',
    borderRadius: '100px',
    fontFamily: language === 'en' ? "'Poppins', sans-serif" : "'Kanit', sans-serif",
    fontSize: '14px',
    color: '#949494',
    cursor: 'pointer',
    transition: 'all 0.3s',
  };

  const btnSmellActive = {
    ...btnSmell,
    border: '1px solid #2e5853',
    color: '#2e5853',
  };

  const handleChoose = (smell) => {
    setActiveSmells((prevSmells) => {
      if (prevSmells.includes(smell)) {
        return prevSmells.filter((item) => item !== smell);
      } else {
        return [...prevSmells, smell];
      }
    });
  };
    // ฟิลเตอร์ textWithTier ตาม selectedTier
  const filteredReviews = selectedProduct?.textWithTier?.filter((review) => {
    return selectedTier ? review.tier === selectedTier : true; // ถ้าเลือก tier จะกรองเฉพาะ tier ที่เลือก
  }) || [];

  const styles = {
    Page: {
      paddingTop:isMobile ? '100px' : '150px',
        width: '100%',
        boxSizing: 'border-box',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        margin: '0 auto',
        gap: '16px',
    },
    content: {
        display: 'flex',
        flexDirection: 'row',
        width: '95%',
        justifyContent: 'space-between',
        gap:'20px',
        overflowY: 'auto', 
        height:'auto',
        boxSizing:'border-box',
         flexDirection: isMobile ? 'column-reverse' : 'row'
    },
    productDetailsImage: {
        width: '300px',
        height: 'auto',
    },
    content2:{
      width:'100%',
      boxSizing:'border-box',
    },
    contentDetail:{
      display: 'flex', 
      flexDirection: 'column', 
      gap: '10px',
      padding: '42px 16px',
      background:'#2E585333',
      borderRadius:'30px',
      marginBottom:'24px'
    },
    DetailReviews:{
      display: 'flex', 
      flexDirection: 'column', 
      gap: '10px',
      padding: '42px 16px',
      background:'#2e5853',
      borderRadius:'30px',
      fontFamily: language === 'en' ? "'Poppins', sans-serif" : "'Kanit', sans-serif",
      boxSizing:'border-box',
      marginBottom:'24px'
    },
    headName:{
      color:'#10443e',
      fontSize:'30px',
      margin:'0',
      fontFamily:  "'Kanit', sans-serif",
    },
    headNameEn:{
      color:'#10443e',
      fontSize:'30px',
      margin:'0',
      fontFamily:  "'Poppins', sans-serif",
    },
    Popular:{
      display:'flex',
      gap:'16px',
      fontSize:'14px',
      color:'#949494',
      alignItems:'center',
      fontFamily:  language === 'en' ?  "'Poppins', sans-serif" :  "'Kanit', sans-serif",
    },
    buyNow:{
      background:'#10443e',
      borderRadius:'18px',
      color:'white',
      border:'none',
      padding:'0 30px',
      margin:'16px 0',
      fontFamily:  language === 'en' ?  "'Poppins', sans-serif" :  "'Kanit', sans-serif",
      fontWeight:'bold',
      fontSize:'16px',
      cursor:'pointer'
    },
    productContainer: {
      flexWrap: 'wrap', // ให้สินค้าแสดงเป็นหลายแถว
      gap: '24px', // ช่องว่างระหว่างสินค้า
      boxSizing:'border-box',
      width: isMobile ? '100%' :'45%',
      height:'100%',
    },
    productImage: {
      width: '100%',
      height: '220px',
      borderRadius: '8px',
      marginBottom: '12px',
      objectFit:'cover'
    },
    DetailPurchase:{
      background:'#ede4d6',
      border:'1px solid #ede4d6',
      borderRadius:'16px',
      padding:'16px',
      color:'#10443E',
      fontSize:'16px',
      fontFamily: language === 'en' ? "'Poppins', sans-serif" : "'Kanit', sans-serif",
    },
    purchaseProduct:{
      display:'flex',
      justifyContent:'space-between',
      padding:'5px 0',
      fontFamily:  language === 'en' ?  "'Poppins', sans-serif" :  "'Kanit', sans-serif",
    },
    purchase:{
      display:'flex',
      gap:'8px',
    },
    ImgIconpurchase:{
      height: 'auto',
      objectFit: 'contain',
    },
    btnProduct: {
      display: 'flex',
      justifyContent: 'center', // จัดกลางปุ่มในแนวนอน
      alignItems: 'center', // จัดกลางปุ่มในแนวตั้ง
      gap:'8px'
    },
    starRating:{
      display: 'flex',
      justifyContent: 'space-between', // จัดกลางปุ่มในแนวนอน
      alignItems: 'center', // จัดกลางปุ่มในแนวตั้ง
    },
    viewMoreButton:{
      background:'#EDE4D6',
      color:'#10443E',
      border:'1px solid #10443E',
      borderRadius:'100px',
      justifyContent: 'space-between',
      alignItems:'center',
      display:'flex',
      fontFamily:  language === 'en' ?  "'Poppins', sans-serif" :  "'Kanit', sans-serif",
      fontSize:'16px',
      width:'100%',
      cursor:'pointer'
    },
    buyNowButton:{
      background:'#10443E',
      border:'1px solid #10443E',
      fontFamily:  language === 'en' ?  "'Poppins', sans-serif" :  "'Kanit', sans-serif",
      borderRadius:'100px',
      fontSize:'16px',
      width:'100%',
      color:'white',
      cursor:'pointer'
    },
    learnMoreButton: {
      marginTop: '20px',
      padding: '16px 32px', // ปรับ padding ให้มีขนาดกว้างขึ้น
      backgroundColor: 'white',
      color: '#10443e',
      border: '1px solid #10443e',
      borderRadius: '1000px',
      cursor: 'pointer',
      fontSize: '18px',
      boxShadow: '0px 4px 16px 0px #10443E33',
      fontFamily: language === 'en' ? "'Poppins', sans-serif" : "'Kanit', sans-serif",
      fontWeight: 'bold',
      alignItems: 'center',
      justifyContent: 'center', // ปรับให้ข้อความอยู่กลางปุ่ม
      display: 'flex',
      textAlign: 'center', // เพื่อให้ข้อความอยู่ตรงกลาง
      gap:'16px',
      width:'100%'
    },
    AddLine:{
      width:'60%' ,
    },
    contentScan:{
      background:'white',
      width:'60%',
      borderRadius:'16px',
      fontFamily: "'Kanit', sans-serif",
      fontSize:'24px',
      textAlign:'center',
      color:'#2e5853',
      fontWeight:'bold',
      padding:'10px'
  },
  contentRight:{
    display: 'flex',
    alignItems: 'center', 
    flexDirection: 'column',   
    textAlign: 'left',
    gap:'10px',
    width:'30%',
    
},
  };

  return (
    <div style={styles.Page}>
      <div style={{
        width:'95%',
        borderBottom:'1px solid #ad9468',
        fontFamily:  language === 'en' ? "'Cormorant Garamond', serif" :  "'Kanit', sans-serif",
        fontWeight:language === 'en' ? 'bold' :  'normal'
        }}>
        <p style={{textAlign:'left',color:'#476b66',fontSize:'72px',margin:0}}>{language === 'en' ? 'Local Product' : 'สินค้า'}</p>
      </div>
      <div style={styles.content}>
      <div style={styles.productContainer}>
  {/* แสดงรายการสินค้า */}
  {products.length > 0 ? (
    <div
      style={{
        display: 'grid',
        gridTemplateColumns: 'repeat(2, 1fr)', // สร้าง 2 คอลัมน์
        gap: '20px', // เว้นระยะห่างระหว่างไอเท็ม
        maxHeight: '150vh', // ความสูงสูงสุดที่ต้องการให้แสดง
        overflowY: 'auto', // ทำให้สามารถเลื่อนในแนวแกน Y
      }}
    >
      {products.map((product) => (
        <div
          key={product.id}
          style={{
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'space-between',
            height: '100%', // ให้สินค้ารวมสูงสุด
            boxSizing: 'border-box',
            borderRadius: '15px',
            padding: '16px',
            backgroundColor: '#ede4d6', // พื้นหลังสินค้า
            textAlign: 'center',
            border: 'none', // กรอบสินค้า
          }}
        >
          {product.images.length > 0 && (
            <img
              src={product.images[0]}
              alt={product.name}
              style={styles.productImage}
            />
          )}
          <p
            style={{
              fontSize: '20px',
              fontFamily: language === 'en' ? "'Kanit', sans-serif" : "'Kanit', sans-serif",
              margin: 0,
            }}
          >
            {product.name} {/* ชื่อสินค้า */}
          </p>
          <p
            style={{
              fontSize: '14px',
              fontFamily: language === 'en' ? "'Kanit', sans-serif" : "'Kanit', sans-serif",
              margin: 0,
              textAlign: 'left',
              color: '#000000B2',
            }}
          >
            {product.caption} {/* คำบรรยายสินค้า */}
          </p>
          <div style={{ flexGrow: 1 }} /> {/* ทำให้เนื้อหายืดเต็มพื้นที่ */}
          <div style={styles.starRating}>
            <p>{product.price}.-</p> {/* ราคา */}
            <div>{renderStars(product.averageTier)}</div> {/* ใช้ฟังก์ชัน renderStars ที่แสดงดาว */}
          </div>
          <div style={styles.btnProduct}>
            <button
              style={styles.viewMoreButton}
              onClick={() => handleProductClick(product.id)}
            >
              {language === 'en' ? 'More' : 'เพิ่มเติม'}
              <img src={circleRight} alt="circleRight" style={{ width: '20%', height: 'auto' }} />
            </button>

            {/* ปุ่มซื้อเลย (mockup) */}
            <button style={styles.buyNowButton}  onClick={handleClick}>
              {language === 'en' ? 'BUY NOW' : 'ซื้อเลย'}
            </button>
          </div>
        </div>
      ))}
    </div>
  ) : (
    <p>{language === 'en' ? 'No products available' : 'ไม่มีสินค้า'}</p>
  )}
</div>

      {selectedProduct ? (
        <div style={styles.content2}>
          <div  style={styles.contentDetail}>
               {/* ส่วนบน: รูปภาพและรายละเอียด */}
          <div style={{ display: 'flex', gap: '20px' }}>
            {/* รูปภาพ */}
            <div
              style={{
                flex: '1',
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
              }}
            >
              {selectedProduct?.images?.length > 0 && (
                <img
                  src={currentImage}
                  alt={selectedProduct?.name}
                  style={{
                    width: '100%',
                    height: '365px',
                    objectFit: 'cover',
                    borderRadius: '10px',
                    marginBottom: '10px',
                  }}
                />
              )}
              <div
                style={{
                  display: 'flex',
                  gap: '10px',
                  overflowX: 'auto', // เพิ่มเลื่อนซ้ายขวา
                  whiteSpace: 'nowrap', // ทำให้ภาพเรียงต่อกัน
                  padding: '10px', // เพิ่มระยะห่างภายใน
                }}
              >
                {selectedProduct?.images?.map((image, index) => (
                  <img
                    key={index}
                    src={image}
                    alt={`${selectedProduct?.name} - ${index + 1}`}
                    style={{
                      width: '100px', // กำหนดขนาดภาพตัวอย่าง
                      height: '100px',
                      cursor: 'pointer',
                      borderRadius: '8px',
                      border: currentImage === image ? '2px solid #007BFF' : '1px solid #CCC',
                      objectFit: 'cover',
                    }}
                    onClick={() => setCurrentImage(image)}
                  />
                ))}
              </div>

            </div>

            

            {/* รายละเอียดสินค้า */}
            <div style={{ flex: '1', textAlign: 'left', padding: '0 20px' }}>
              <h2 style={styles.headName}>{selectedProduct?.name}</h2>
              <h3 style={styles.headNameEn}>{selectedProduct?.nameEN}</h3>
              <p style={{...styles.headNameEn, fontWeight: 'bold'}}>{selectedProduct?.price}.-</p>
              <div style={styles.Popular}>
              <p style={{ margin: 0 }}>
                {language === 'en' ? 'Popular' : 'ความนิยม'} {averageStars} / 5 {renderStars(averageStars)} {/* เรียกฟังก์ชัน renderStars ที่แสดงดาว */}
              </p>
            </div>
              <div style={styles.Popular}>
                <p style={{ margin: 0 }}>{language === 'en' ? 'Shipping' : 'ค่าจัดส่ง'}</p>
                <FontAwesomeIcon icon={faTruck} style={{ color: '#949494', fontSize: '14px' }} />
                <p>30.-</p>
              </div>
              {(selectedProduct?.smellEN && selectedProduct.smellEN.length > 0) || (selectedProduct?.smellTH && selectedProduct.smellTH.length > 0) ? (
                <div style={styles.Popular}>
                  <p style={{ margin: 0 }}>{language === 'en' ? 'Smell' : 'กลิ่น'}</p>
                  {/* วนลูปสร้างปุ่มกลิ่นตามภาษาที่เลือก */}
                  {(language === 'en' ? selectedProduct?.smellEN : selectedProduct?.smellTH).map((smell, smellIndex) => (
                    <button
                      key={smellIndex} // ใช้ key ที่ไม่ซ้ำกัน
                      style={activeSmells.includes(smell) ? btnSmellActive : btnSmell}
                      onClick={() => handleChoose(smell)}
                    >
                      {smell}
                    </button>
                  ))}
                </div>
              ) : null}
                  <button style={styles.buyNow} onClick={handleClick}>
                    <p style={{margin: 0}}>{language === 'en' ? 'BUY NOW' : 'ซื้อเลย'}</p>
                  </button>
              <div style={styles.DetailPurchase}>
                <p style={{fontWeight:'bold',margin:0}}>{language === 'en' ? 'When you purchase this product' : 'สินค้าดีต่อสุขภาพ'}</p>
                <div style={styles.purchaseProduct}>
                  <div style={styles.purchase}>
                    <img src={CommunityIcon} alt="CommunityIcon" style={{...styles.ImgIconpurchase,width:'20px'}} />
                    <p style={{margin:0}}>{language === 'en' ? 'Healthy products' : 'สร้างรายได้ให้ชุมชน'}</p>
                  </div>
                  <img src={starReviews5} alt="starReviews5" style={{...styles.ImgIconpurchase,width:'20%'}} />
                </div>
                <div style={styles.purchaseProduct}>
                  <div style={styles.purchase}>
                    <img src={MoneyIcon} alt="MoneyIcon" style={{...styles.ImgIconpurchase,width:'20px'}} />
                    <p style={{margin:0}}>{language === 'en' ? 'Create income for the community' : 'เป็นมิตรต่อสิ่งแวดล้อม'}</p>
                  </div>
                  <img src={starReviews3} alt="starReviews3" style={{...styles.ImgIconpurchase,width:'20%'}} />
                </div>
                <div style={styles.purchaseProduct}>
                  <div style={styles.purchase}>
                    <img src={EcoIcon} alt="EcoIcon" style={{...styles.ImgIconpurchase,width:'20px'}} />
                    <p style={{margin:0}}>{language === 'en' ? 'Environmentally friendly' : 'ได้รับมาตรฐาน'}</p>
                  </div>
                  <img src={starReviews5} alt="starReviews5" style={{...styles.ImgIconpurchase,width:'20%'}} />
                </div>
                <div style={styles.purchaseProduct}>
                  <div style={styles.purchase}>
                    <img src={EcoIcon} alt="EcoIcon" style={{...styles.ImgIconpurchase,width:'20px'}} />
                    <p style={{margin:0}}>{language === 'en' ? 'When you purchase this product' : 'สินค้าดีต่อสุขภาพ'}Standardized</p>
                  </div>
                  <img src={starReviews4} alt="starReviews4" style={{...styles.ImgIconpurchase,width:'20%'}}/>
                </div>
              </div>
            </div>
          </div>

          {/* คำอธิบายสินค้า */}
          <div style={{
            padding: '10px 20px',
            textAlign: 'left',
            lineHeight: '1.5',
            color:'#2e5853',
            fontFamily: language === 'en' ? "'Poppins', sans-serif" : "'Kanit', sans-serif",
          }}>
            <p style={{
              fontSize:'30px',
              fontWeight:'bold',
              marginTop:'0',
              }}> {language === 'en' ? 'Description' : 'รายละเอียด'}</p>
            <p style={{ margin: 0 }}>
              {language === 'en' ? (
                selectedProduct?.descriptionEN.includes('-') 
                  ? selectedProduct?.descriptionEN.split('-').map((text, index) => (
                      <React.Fragment key={index}>
                        {index > 0 && (
                          <>
                            <br /> {/* เพิ่มบรรทัดใหม่ */}
                            <br /> {/* เพิ่มช่องว่าง 1 บรรทัด */}
                          </>
                        )}
                        {index === 0 ? text : `-${text}`} {/* แสดงเครื่องหมาย - ก่อนข้อความ */}
                      </React.Fragment>
                    ))
                  : selectedProduct?.descriptionEN
              ) : (
                selectedProduct?.descriptionTH.includes('-')
                  ? selectedProduct?.descriptionTH.split('-').map((text, index) => (
                      <React.Fragment key={index}>
                        {index > 0 && (
                          <>
                            <br /> {/* เพิ่มบรรทัดใหม่ */}
                            <br /> {/* เพิ่มช่องว่าง 1 บรรทัด */}
                          </>
                        )}
                        {index === 0 ? text : `-${text}`} {/* แสดงเครื่องหมาย - ก่อนข้อความ */}
                      </React.Fragment>
                    ))
                  : selectedProduct?.descriptionTH
              )}
            </p>
          </div>
          </div>

          {/* รีวิว */}
          <div style={styles.DetailReviews}>
            {/* ปุ่มให้เลือก tier */}
            <div style={{display:'flex' , alignItems:'flex-end',gap:'24px'}}>
              <div style={{flexDirection:'column'}}>
                <p style={{margin:0,fontSize:'12px',color:'white'}}>
                  {language === 'en' ? 'Popular' : 'ความนิยม'} {averageStars} / 5     
                  </p>
                  {renderStars(averageStars)} 
              </div>
              <div style={{gap:'10px',display:'flex',fontFamily:"'Poppins', sans-serif" ,}}>
                  <button onClick={() => handleTierSelect(null)} style={{
                     backgroundColor: selectedTier === null ? '#f0f0f0' : 'transparent',
                      border: '0.5px solid #fff',
                      cursor: 'pointer',
                      borderRadius:'3px',
                      padding:'2px 10px',
                      color: selectedTier === null ? '#2e5853' : 'white',
                  }}>All</button> {/* ปุ่มให้แสดงทุก tier */}
                  <div style={{ display: 'flex', gap: '10px' }}>
                    {Array.from({ length: 5 }, (_, index) => {
                      const tier = 5 - index; // เรียงจาก 5 ดาวลงมาที่ 1 ดาว
                      const reviewCount = countReviewsByTier(tier); // คำนวณจำนวนรีวิวใน tier นี้
                      return (
                        <button
                          key={index}
                          onClick={() => handleTierSelect(tier)}
                          style={{
                            backgroundColor: selectedTier === tier ? '#f0f0f0' : 'transparent', // เปลี่ยนสีพื้นหลังเมื่อเลือก
                            border: '0.5px solid #fff',
                            cursor: 'pointer',
                            borderRadius:'3px',
                            padding:'2px 10px',
                            color: selectedTier === tier ? '#2e5853' : 'white',
                          }}
                        >
                          <span> {tier} star</span> {/* แสดงข้อความที่มีจำนวนดาว */}
                          <span> ({reviewCount})</span> {/* แสดงจำนวนรีวิว */}
                        </button>
                      );
                    })}
                  </div>
                </div>
              </div>
              <div style={{ maxHeight: '200px', overflowY: 'auto',     
              scrollbarWidth: "thin",scrollbarColor: "white transparent", }}>
              {filteredReviews.length > 0 ? (
                    filteredReviews.map((review, index) => (
                      <div
                        key={index}
                        style={{
                          padding: '15px',
                          borderBottom: filteredReviews.length > 1 && index < filteredReviews.length - 1 ? '1px solid #ddd' : 'none',
                          display: 'flex',
                          alignItems: 'flex-start',
                          fontFamily:  "'Kanit', sans-serif",
                          boxSizing:'border-box'
                        }}
                      >
                        {/* Default profile image */}
                        <div style={{ marginRight: '15px' }}>
                          <img
                            src={defaultProfile} // ใส่ path รูปภาพ default
                            alt="Default User"
                            style={{ width: '40px', height: '40px', borderRadius: '50%' }}
                          />
                        </div>

                        {/* Content of review */}
                        <div style={{ flex: 1 }}>
                          <div style={{ display: 'flex', alignItems: 'flex-start', marginBottom: '5px',flexDirection:'column' }}>
                          <span style={{color:'white',fontSize:'12px'}}>
                                {language === 'en' ? `User ${String(index + 1).padStart(2, '0')}` : `ผู้ใช้ ${String(index + 1).padStart(2, '0')}`}
                            </span>
                            <div>
                            {renderStars(review.tier)} {/* แสดงดาวตามค่า tier */}
                            </div>
                            <p style={{ margin: '0', color: '#fff',fontSize:'24px' }}>{review.review}</p> {/* ข้อความรีวิว */}
                          </div>
                         
                        </div>
                      </div>
                    ))
                  ) : (
                    <p style={{ color: '#fff' }}>
                      {language === 'en' ? 'No reviews available' : 'ไม่มีรีวิว'}
                    </p>
                  )}
              </div>
                </div>
              </div>
            ) : (
              null
            )}
      </div>
      {showModal && (
        <div 
          style={{
            position: 'fixed', 
            top: '0', 
            left: '0', 
            width: '100%', 
            height: '100%', 
            backgroundColor: 'rgba(0, 0, 0, 0.5)', 
            display: 'flex', 
            justifyContent: 'center', 
            alignItems: 'center',
            zIndex: '1005'
          }}
        >
          <div style={styles.contentRight}>
            <div style={{...styles.contentScan, position: 'relative'}}> {/* เพิ่ม position: relative */}
              {/* ปุ่มปิดที่มุมขวาบนของ contentScan */}
              <button 
                style={{
                  position: 'absolute', 
                  top: '-20px', 
                  right: '-10px', 
                  background: 'white', 
                  border: '1px solid #10443e', 
                  color: '#10443e', 
                  fontSize: '24px', 
                  cursor: 'pointer',
                  zIndex: '1010',
                  padding:'4px 10px',
                  borderRadius:'100%'
                }} 
                onClick={() => setShowModal(false)}
              >
                <FontAwesomeIcon 
                  icon={faXmark} 
                />
              </button>

              <img src={qrCode} alt="qrCode" style={{width: '100%', borderRadius: '16px'}} />
              <p style={{margin: 0}}>สั่งซื้อผ่าน Line (Order via Line)</p>
              <p style={{margin: 0}}>Tel. 092-5054994</p>
            </div>

            <div style={styles.AddLine}>
              <button 
                style={{
                  ...styles.learnMoreButton,
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  position: 'relative',
                }}
                onClick={() => window.open('https://www.line.me/th/', '_blank')}
              >
                <span style={{flex: 1, textAlign: 'center'}}>
                  {language === 'en' ? 'ADD LINE' : 'ADD LINE'}
                </span>
                <FontAwesomeIcon 
                  icon={faCircleChevronRight} 
                  style={{
                    fontSize: '18px',
                    position: 'absolute',
                    right: '16px',
                  }}
                />
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default ProductPage;
